@import url(https://fonts.googleapis.com/css2?family=Klee+One:wght@600&family=Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.App {
  background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  font-family: "Rubik", sans-serif;
}

#img {
  margin-top: 50px;
  width: 250px;
  height: 150px;
  /* 
  width:15rem;
  margin: -15rem;
  position: relative;
  top: -20rem; */
}

.card-container {
  width: 500px;
  margin: 15px;
  height: 1000px;
  background-color: orange;
  padding: 20px;
  border-radius: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 4fr;
  grid-gap: 10px;
  overflow: hidden;
}

.front-flip {
  margin: 20px;
  text-align: center;
  background-color: #ffff;
  border-radius: 15px;
  width: 150px;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: box-shadow 0.3s;
  padding: 30px;
}

.front-flip:hover {
  cursor: pointer;
  box-shadow: 0 0 5px 5px rgb(44, 46, 218);
}

.card-header {
  text-align: center;
  font-size: 1.2rem;
  top: 30px;
}

.card-image {
  width: 70px;
  height: 50px;
  margin: auto;
}
.card-image:hover {
  width: 100px;
  height: 100px;
}

.card-title {
  font-size: 2rem;
  color: black;
  text-align: center;
  margin-bottom: 30px;
}
